.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.Main {
  background-color: #f2f4fb;
  text-align: center;

  display: block;
  margin-left: auto;
  margin-right: auto;
  min-height: 100vh;
  width: min(500px, 100%);
  box-shadow: 10px 20px 40px rgba(0, 0, 0, 0.1),
    -10px 20px 40px rgba(0, 0, 0, 0.1);

  padding-bottom: 10px;
}

#footer {
  position: fixed;
  bottom: 0;
}

@font-face {
  font-family: "BinggraeSamanco-Bold";
  src: url("./fonts/BinggraeSamanco-Bold.otf");
}

@font-face {
  font-family: "BMYEONSUNG";
  src: url("./fonts/BMYEONSUNG.otf");
}
