.header {
  display: inline-block;

  background-color: #5aa17f;

  justify-content: center; /* 수평 중앙 정렬 */
  align-items: center; /* 수직 중앙 정렬 */

  display: block;
  width: inherit;
  height: 50px;
  text-align: center;
  font-size: 30px;
  font-family: "BinggraeSamanco-Bold";
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.4);
}

.header_text {
  text-align: center;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}
